.login {
  width: 100vw;
  height: 100vh;
  background: linear-gradient(
      rgba(255, 255, 255, 0.5),
      rgba(255, 255, 255, 0.5)
    ),
    url("https://scontent.fkhi17-1.fna.fbcdn.net/v/t1.6435-9/193785784_5728869680518461_3976951054172780680_n.jpg?_nc_cat=108&ccb=1-7&_nc_sid=a26aad&_nc_ohc=JwX7RE3M7MQAX-6wjY5&_nc_ht=scontent.fkhi17-1.fna&oh=00_AT9EOWyLi8W7JQgA-gWe_4Zfz4LmFok7P11QjdNxU0yLYQ&oe=62EF7A43")
      center;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
}
.login__container {
  width: 25%;
  padding: 20px;
  background-color: white;
  
  display: flex;
  flex-direction: column;
  text-align: center;
}
.login__textBox {
  flex: 1;
min-width: 40%;
   margin: 12px 0;
   padding: 10px;
  font-size: 18px;
  margin-bottom: 10px;
}
.login__btn:hover {
color:black !important;
background-color: #ea4b7e99;
}

.login__btn {
  padding: 10px;
  font-size: 20px;
  margin-top: 10px;
  margin-bottom: 10px;
  border: none;
  text-decoration: none;
  background-color:   #ee719999  ;
}
.login__google {
  margin-bottom: 8px;
  font-size: 20px;
  background-color:   rgb(112, 185, 185) ;
  color:rgb(255, 255, 255);
}
.login__google:hover{
  background-color: rgb(63, 159, 159);
}
.login div {
  margin-top: 7px;
}
.forgot{
  margin: 5px 0px;
   font-size: 19px;
   text-decoration: underline;
   cursor: pointer;
}